.subscription {

  background-color: $color-primary-100;

  &__content {
    display: flex;
    align-items: center;
    gap: 95px;
    padding: 40px 0 46px;
  }

  &__title {
    min-width: 429px;
    color: $color-primary-200;
  }

  &__title > h4 {
    margin-bottom: 20px;
  }

  &__title > span {
    font-size: 15px;
    line-height: 20px;
  }

  &__input {
    width: 100%;
    display: flex;
    align-items: center;
    
  }

  &__input > form {
    display: flex;
    align-items: center;
    width: 100%;
    gap: 10px;
  }

  &__input > form > input {
    width: 100%;
    height: 54px;
    padding: 15px 22px 19px 16px;
    border-radius: 15px;
  }

  &__input > input::placeholder {
    font-size: 15px;
    line-height: 20px;
    color: $color-primary-200;
  }
}
