.catalog {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(295px, 1fr));
  gap: 20px;

  &__card {
    min-height: 366px;
    transition: 300ms;
    border-radius: 18px;
    box-shadow: 0px 0px 18px 0px #44444417;

    &:hover {
      cursor: pointer;
      opacity: 90%;

      .catalog__card-title {
        text-decoration: underline;
      }
    }
  }

  &__card--sold {
    position: relative;

    .catalog__card-img-box::before {
      position: absolute;
      content: "SOLD";
      top: 25%;
      left: 50%;
      transform: translateX(-50%);
      color: #fff;
      font-family: "Poppins";
      font-size: 50px;
      font-weight: 700;
      line-height: 75px;
    }

    .catalog__card-description {
      opacity: 50%;
    }
  }

  &__card--new {
    .catalog__card-img-box {
      position: relative;
    }

    .catalog__card-img-box::after {
      position: absolute;
      content: url(/images/catalog/New.svg);
      left: 0;
      bottom: 0;
    }
  }

  &__card-img-box > img:active {
      transform: scale(1.5);
      transition: transform 300ms;
  }

  &__card-description {
    padding: 20px;
  }

  &__card-title {
    display: flex;
    font-size: 17px;
    gap: 8px;
    margin-bottom: 16px;
  }

  &__card-title > span {
    line-height: 26px;
    color: $color-secondary-2;
  }

  &__card-title > h6 {
    line-height: 24px;
  }

  &__card-desc {
    font-size: 14px;
    line-height: 19px;
    color: $color-secondary-2;
    margin-bottom: 26px;
  }

  &__card-price > h5 {
    line-height: 27px;
  }
}
